module.exports = "<div class=section><title-link>Buttons</title-link><div class=section__content><p>The button component allows to easily toggle classes. It supports the <a href=#ripple-effect><code>ripple-effect</code></a></p><div class=\"flex start wrap\"><mdl-button class=space>Button</mdl-button><mdl-button v-mdl-ripple-effect=v-mdl-ripple-effect class=space>Ripple Effect</mdl-button><mdl-button disabled=disabled class=space>Disabled</mdl-button><mdl-button icon=icon class=space><i class=material-icons>star</i></mdl-button></div><pre><code class=html><p>&lt;mdl-button&gt;Button&lt;/mdl-button&gt;</p><p>&lt;mdl-button v-mdl-ripple-effect&gt;Ripple Effect&lt;/mdl-button&gt;</p><p>&lt;mdl-button disabled&gt;Disabled&lt;/mdl-button&gt;</p><p>&lt;mdl-button icon&gt;</p><p>  &lt;i class=&quot;material-icons&quot;&gt;star&lt;/i&gt;</p><p>&lt;/mdl-button&gt;</p></code></pre><h5>Playground</h5><div class=\"flex start wrap\"><mdl-button :colored=colored :raised=raised :fab=fab :mini-fab=miniFab :primary=primary :accent=accent :icon=icon :disabled=disabled><i v-if=iconText class=material-icons>{{iconText}}</i><span v-else=v-else>{{text}}</span></mdl-button></div><div class=\"flex center wrap\"><mdl-checkbox :checked.sync=colored class=\"reset-width space\">Colored</mdl-checkbox><mdl-checkbox :checked.sync=raised class=\"reset-width space\">Raised</mdl-checkbox><mdl-checkbox :checked.sync=disabled class=\"reset-width space\">Disabled</mdl-checkbox><mdl-checkbox :checked.sync=icon class=\"reset-width space\">Icon</mdl-checkbox><mdl-checkbox :checked.sync=accent class=\"reset-width space\">Accent</mdl-checkbox><mdl-checkbox :checked.sync=primary class=\"reset-width space\">Primary</mdl-checkbox><mdl-checkbox :checked.sync=miniFab class=\"reset-width space\">Mini Fab</mdl-checkbox><mdl-checkbox :checked.sync=fab class=\"reset-width space\">Fab</mdl-checkbox></div><div class=\"flex start wrap\"><mdl-textfield :value.sync=text label=Text floating-label=floating-label class=space></mdl-textfield><mdl-textfield :value.sync=iconText label=Icon floating-label=floating-label class=space></mdl-textfield></div><p>Result:</p><pre><code v-hljs=playgroundHtml class=html></code></pre><h5>Link buttons</h5><p>Sometimes you might want to display a link as a button. In order to do this,</p> you can use the <code>mdl-anchor-button</code> element. It works exactly as the button does but uses an <code>a</code> html link instead of a <code>button</code>.<div class=\"flex start wrap\"><mdl-anchor-button href=http://vuejs.org/api target=_blank>Open Vuejs doc</mdl-anchor-button></div><pre><code class=html><p>&lt;mdl-anchor-button href=&quot;http://vuejs.org/api&quot; target=&quot;_blank&quot;&gt;Open Vuejs doc&lt;/mdl-anchor-button&gt;</p></code></pre><h5>Prop List</h5><table class=\"mdl-data-table mdl-js-data-table\"><thead><tr><th class=mdl-data-table__cell--non-numeric>Prop</th><th class=mdl-data-table__cell--non-numeric>Description</th><th class=mdl-data-table__cell--non-numeric>Remarks</th></tr></thead><tbody><tr><td class=mdl-data-table__cell--non-numeric><code>disabled</code></td><td class=mdl-data-table__cell--non-numeric>Disables the button. Adds the <code>is-disabled</code> class to the button</td><td class=mdl-data-table__cell--non-numeric></td></tr><tr><td class=mdl-data-table__cell--non-numeric><code>raised</code></td><td class=mdl-data-table__cell--non-numeric>Applies<i> raised</i> display effect with <code>mdl-button--raised</code> class</td><td class=mdl-data-table__cell--non-numeric>Mutually exclusive with fab, mini-fab, and icon</td></tr><tr><td class=mdl-data-table__cell--non-numeric><code>fab</code></td><td class=mdl-data-table__cell--non-numeric>Applies<i> fab</i> (circular) display effect with <code>mdl-button--fab</code> class</td><td class=mdl-data-table__cell--non-numeric>Mutually exclusive with raised, mini-fab, and icon</td></tr><tr><td class=mdl-data-table__cell--non-numeric><code>mini-fab</code></td><td class=mdl-data-table__cell--non-numeric>Applies<i> mini-fab</i> (small fab circular) display effect with <code>mdl-button--mini-fab</code> class</td><td class=mdl-data-table__cell--non-numeric>Mutually exclusive with raised, fab, and icon</td></tr><tr><td class=mdl-data-table__cell--non-numeric><code>icon</code></td><td class=mdl-data-table__cell--non-numeric>Applies<i> icon</i> (small plain circular) display effect with <code>mdl-button--icon</code> class</td><td class=mdl-data-table__cell--non-numeric>Mutually exclusive with raised, fab and mini-fab</td></tr><tr><td class=mdl-data-table__cell--non-numeric><code>colored</code></td><td class=mdl-data-table__cell--non-numeric>Applies<i> colored</i> display effect with <code>mdl-button--colored</code> class</td><td class=mdl-data-table__cell--non-numeric></td></tr><tr><td class=mdl-data-table__cell--non-numeric><code>primary</code></td><td class=mdl-data-table__cell--non-numeric>Applies<i> primary</i> color display effect with <code>mdl-button--primary</code> class</td><td class=mdl-data-table__cell--non-numeric></td></tr><tr><td class=mdl-data-table__cell--non-numeric><code>accent</code></td><td class=mdl-data-table__cell--non-numeric>Applies<i> accent</i> color display effect with <code>mdl-button--accent</code> class</td><td class=mdl-data-table__cell--non-numeric></td></tr></tbody></table><br/></div></div>";


