module.exports = "<div class=section><title-link>Ripple Effect</title-link><div class=section__content><p>Add the ripple effect by adding the <code>mdl-js-ripple-effect</code> class to any component that supports it:<ul><li>Radio Buttons</li><li>Buttons</li><li>Checkboxes</li><li>Icon Toggles</li><li>Switches</li></ul></p><div class=\"flex start wrap\"><mdl-radio :checked.sync=radio value=radio class=mdl-js-ripple-effect>Radio</mdl-radio></div><div class=\"flex start wrap\"><mdl-button raised=raised colored=colored class=mdl-js-ripple-effect>Button</mdl-button></div><div class=\"flex start wrap\"><mdl-checkbox :checked.sync=check class=\"reset-width mdl-js-ripple-effect\">Checkbox</mdl-checkbox></div><div class=\"flex start wrap\"><mdl-icon-toggle :checked.sync=check icon=format_bold class=mdl-js-ripple-effect></mdl-icon-toggle></div><div class=\"flex start wrap\"><mdl-switch :checked.sync=check class=\"reset-width mdl-js-ripple-effect\">Switch</mdl-switch></div><p>At the moment, the <code>v-mdl-ripple-effect</code> just adds the class for you, so there is not much benefit from using it. This directive may disappear in v1</p></div></div>";


