module.exports = "<div class=section><title-link>Usage</title-link><div class=section__content><p>If you use a module bundler like<a href=https://webpack.github.io/ > webpack</a> or<a href=http://browserify.org/ > Browserify</a> with babel (es6 support), you can import it and use it as a plugin:</p><pre><code class=javascript>import VueMdl from 'vue-mdl'\n\nVue.use(VueMdl)\n</code></pre><p>If you don't use es6, you'll have to use it this way:</p><pre><code class=javascript>// es5 webpack/browserify\nvar VueMdl = require('vue-mdl')\n\nVue.use(VueMdl.default)\n</code></pre><p>Using it as a plugin will include all components and directives. Vue MDL also exports a components and a directives properties containing all components and directives:</p><pre><code class=javascript>// es6\nimport { components, directives } from 'vue-mdl'\n\nnew Vue({\n  components,\n  directives\n})\n\n// es5\nvar VueMdl = require('vue-mdl')\n\nnew Vue({\n  components: VueMdl.components,\n  directives: VueMdl.directives\n})\n</code></pre><p>You can also import individually any component or directive</p><pre><code class=javascript>// es6\nimport { MdlCheckbox, MdlBadge } from 'vue-mdl'\n\nnew Vue({\n  components: {\n    MdlCheckbox\n  },\n  directives: {\n    MdlBadge\n  }\n})\n\n// es5\nvar VueMdl = require('vue-mdl')\n\nnew Vue({\n  components: {\n    MdlCheckbox: VueMdl.MdlCheckbox\n  },\n  directives: {\n    MdlBadge: VueMdl.MdlBadge\n  }\n})</code></pre></div></div>";


