module.exports = "<div class=section _v-2245405e=\"\"><title-link _v-2245405e=\"\">Cards</title-link><div class=section__content _v-2245405e=\"\"><p _v-2245405e=\"\">Cards are self-contained pieces of paper with data. Vue MDL tries to make them easier to use in common cases. If it doesn't fit your needs, you can always overwrite the content using slots. Cards are not finished yet but you can use can always so using slots may be necessary</p><div class=\"flex start wrap\" _v-2245405e=\"\"><mdl-card :title=cardTitle :supporting-text=supportingText actions=actions actions-text=\"View updates\" class=\"reset-height white-space\" _v-2245405e=\"\"></mdl-card></div><div class=\"flex start wrap\" _v-2245405e=\"\"><mdl-textfield :value.sync=cardTitle floating-label=\"Card Title\" _v-2245405e=\"\"></mdl-textfield><mdl-textfield textarea=textarea :value.sync=supportingText floating-label=\"Supporting Text\" _v-2245405e=\"\"></mdl-textfield></div><p _v-2245405e=\"\">Pass the text you want to the directive:</p><pre _v-2245405e=\"\"><code class=html _v-2245405e=\"\"><p _v-2245405e=\"\">&lt;mdl-card :title=\"cardTitle\" :supporting-text=\"supportingText\" actions=\"actions\" actions-text=\"View updates\"&gt;</p><p _v-2245405e=\"\">&lt;/mdl-card&gt;</p></code></pre><div class=\"flex start wrap\" _v-2245405e=\"\"><mdl-card title=Welcome :supporting-text=supportingText actions=actions actions-text=\"Get started\" menu=menu class=demo-card-welcome _v-2245405e=\"\"></mdl-card></div><br _v-2245405e=\"\"><pre _v-2245405e=\"\"><code class=css _v-2245405e=\"\">.demo-card-welcome.mdl-card {\n  width: 512px;\n}\n.demo-card-welcome .mdl-card__title,\n.demo-card-welcome .mdl-card__menu {\n  color: white;\n}\n.demo-card-welcome .mdl-card__title {\n  height: 176px;\n  background: url('http://www.getmdl.io/assets/demos/welcome_card.jpg') center / cover;\n}</code><code class=html _v-2245405e=\"\"><p _v-2245405e=\"\">&lt;mdl-card class=\"demo-card-welcome\" title=\"Welcome\" :supporting-text=\"supportingText\" actions=\"actions\" actions-text=\"Get started\"&gt;</p><p _v-2245405e=\"\">&lt;/mdl-card&gt;</p></code></pre><p _v-2245405e=\"\">You can replace any of the existing content by using slots. Available slots are <code _v-2245405e=\"\">title</code>, <code _v-2245405e=\"\">subtitle</code>, <code _v-2245405e=\"\">supporting-text</code>, <code _v-2245405e=\"\">media</code>, <code _v-2245405e=\"\">actions</code> and <code _v-2245405e=\"\">menu</code></p><div class=\"flex start wrap\" _v-2245405e=\"\"><mdl-card class=demo-card-image _v-2245405e=\"\"><div slot=title class=\"mdl-card__title mdl-card--expand\" _v-2245405e=\"\"></div><div slot=actions class=mdl-card__actions _v-2245405e=\"\"><span class=demo-card-image__filename _v-2245405e=\"\">Image.jpg</span></div></mdl-card></div><table class=\"mdl-data-table mdl-js-data-table\" _v-2245405e=\"\"><thead _v-2245405e=\"\"><tr _v-2245405e=\"\"><th class=mdl-data-table__cell--non-numeric _v-2245405e=\"\">Prop</th><th class=mdl-data-table__cell--non-numeric _v-2245405e=\"\">Description</th><th class=mdl-data-table__cell--non-numeric _v-2245405e=\"\">Remarks</th></tr></thead><tbody _v-2245405e=\"\"><tr _v-2245405e=\"\"><td class=mdl-data-table__cell--non-numeric _v-2245405e=\"\"><code _v-2245405e=\"\">title</code></td><td class=mdl-data-table__cell--non-numeric _v-2245405e=\"\">Defines card's title</td><td class=mdl-data-table__cell--non-numeric _v-2245405e=\"\"></td></tr><tr _v-2245405e=\"\"><td class=mdl-data-table__cell--non-numeric _v-2245405e=\"\"><code _v-2245405e=\"\">supporting-text</code></td><td class=mdl-data-table__cell--non-numeric _v-2245405e=\"\">Defines card's supporting text</td><td class=mdl-data-table__cell--non-numeric _v-2245405e=\"\"></td></tr><tr _v-2245405e=\"\"><td class=mdl-data-table__cell--non-numeric _v-2245405e=\"\"><code _v-2245405e=\"\">actions</code></td><td class=mdl-data-table__cell--non-numeric _v-2245405e=\"\">Makes the actions section visible</td><td class=mdl-data-table__cell--no