module.exports = "<div class=section><title-link>Textfields</title-link><div class=section__content><p>The textfield component is highly customizable and allows you to use text inputs and textareas. Instead of using the <code>v-model</code> directive you have to pass a two way prop named <code>value</code>.</p><div class=\"flex center wrap\"><p>Hello {{name}}</p></div><div class=\"flex start wrap\"><mdl-textfield label=Name :value.sync=name class=space></mdl-textfield><mdl-textfield floating-label=Name :value.sync=name class=space></mdl-textfield></div><pre><code class=html><p>&lt;mdl-textfield label=&quot;Name&quot; :value.sync=&quot;name&quot;&gt;&lt;/mdl-textfield&gt;</p><p>&lt;mdl-textfield floating-label=&quot;Name&quot; :value.sync=&quot;name&quot;&gt;&lt;/mdl-textfield&gt;</p></code></pre><p>Use the <code>textarea</code> prop to use a textarea instead of an input</p><div class=\"flex start wrap\"><mdl-textfield floating-label=Textarea textarea=textarea rows=4 :value.sync=text></mdl-textfield><p>{{text}}</p></div><pre><code class=html>&lt;mdl-textfield floating-label=&quot;Name&quot; textarea rows=&quot;4&quot;&gt;&lt;/mdl-textfield&gt;</code></pre><p>You can dynamically bind the <code>floating-label</code> prop and use the <code>label</code>prop for the real label if you need to control whether the label can floats or not</p><pre><code class=html>&lt;mdl-textfield :floating-label=&quot;float&quot; label=&quot;Name&quot; textarea&gt;&lt;/mdl-textfield&gt;</code><code class=javascript>new Vue({\n  el: '#app',\n  data: {floating: false}\n})\n</code></pre><p>You can make the textfield expandable by using the <code>expandable</code> prop and setting and <code>id</code> on the element.</p><div class=\"flex start wrap\"><mdl-textfield id=demo-expandable-button expandable=search></mdl-textfield></div><pre><code class=html>&lt;mdl-textfield id=&quot;demo-expandable-button&quot; expandable=&quot;search&quot;&gt;&lt;/mdl-textfield&gt;</code></pre><h5>Prop List</h5><table class=\"mdl-data-table mdl-js-data-table\"><thead><tr><th class=mdl-data-table__cell--non-numeric>Prop</th><th class=mdl-data-table__cell--non-numeric>Description</th><th class=mdl-data-table__cell--non-numeric>Remarks</th></tr></thead><tbody><tr><td class=mdl-data-table__cell--non-numeric><code>value</code></td><td class=mdl-data-table__cell--non-numeric>Defines a value for the textfield</td><td class=mdl-data-table__cell--non-numeric>Should be used with the <code>.sync</code> modifier</td></tr><tr><td class=mdl-data-table__cell--non-numeric><code>label</code></td><td class=mdl-data-table__cell--non-numeric>Defines the label used on the the textfield</td><td class=mdl-data-table__cell--non-numeric>You don't need this if you use <code>floating-label</code></td></tr><tr><td class=mdl-data-table__cell--non-numeric><code>floating-label</code></td><td class=mdl-data-table__cell--non-numeric>Defines whether the label should float or not. If the given value is a String, you don't need to provide a <code>label</code> prop</td><td class=mdl-data-table__cell--non-numeric></td></tr><tr><td class=mdl-data-table__cell--non-numeric><code>textarea</code></td><td class=mdl-data-table__cell--non-numeric>Makes the textfield a textarea instead of an input</td><td class=mdl-data-table__cell--non-numeric></td></tr><tr><td class=mdl-data-table__cell--non-numeric><code>rows</code></td><td class=mdl-data-table__cell--non-numeric>Controls the number of rows used in the textarea</td><td class=mdl-data-table__cell--non-numeric>Needs the <code>textarea</code> prop</td></tr><tr><td class=mdl-data-table__cell--non-numeric><code>pattern</code></td><td class=mdl-data-table__cell--non-numeric>Input validation using a Regex</td><td class=mdl-data-table__cell--non-numeric>Only works on non-textareas</td></tr><tr><td class=mdl-data-table__cell--non-numeric><code>error</code></td><td class=mdl-data-table__cell--non-numeric>Defines the error displayed when the input text doesn't match the given pattern</td><td class=mdl-data-table__cell--non-numeric>Needs the <code>pattern</code> prop</td></tr><tr><td class=mdl-data-table__cell--non-numeric><code>expandable</code></td><td class=mdl-data-table__cell--non-numeric>Makes the textfield look like a button that expands. You need to pass a material icon to this prop</td><td class=mdl-data-table__cell--non-numeric>Needs to have an id set</td></tr><tr><td class=mdl-data-table__cell--non-numeric><code>type</code></td><td class=mdl-data-table__cell--non-numeric>Defines the type of the input</td><td class=mdl-data-table__cell--non-numeric>Defaults to text. Only works on non-textarea</td></tr></tbody></table><br/></div></div>";


