module.exports = "<div class=section><title-link>Selects</title-link><div class=section__content><p>Selects are based on the<a href=https://github.com/CreativeIT/getmdl-select/ target=_blank> getmdl-select</a> and are supposed to replace the &nbsp;<code>select</code> html element.</p><div class=\"flex center wrap\"><mdl-select id=country-select label=Country :value.sync=country :options=countriesArray></mdl-select></div><div class=\"flex center wrap\"><p>Selected country is {{country}}</p></div><pre><code class=html><p>&lt;mdl-select label=&quot;Country&quot; id=&quot;contry-select&quot; :value.sync=&quot;country&quot; :options=&quot;countriesArray&quot;&gt;&lt;/mdl-select&gt;</p></code><code class=javascript>new Vue({\n  data: {\n    country: 'Spain',\n    countryArrays: ['Russia', 'Spain', 'France', 'China']\n  }\n})\n</code></pre><p>Provide the options as an array of Values or as an array of objects with a&nbps;<code>name</code> and a &nbsp;<code>value</code> properties.</p><div class=\"flex center wrap\"><mdl-select id=country-select-2 label=Country :value.sync=countryVal :options=countries></mdl-select></div><div class=\"flex center wrap\"><p>Selected country is {{countryVal}}</p></div><pre><code class=html><p>&lt;mdl-select label=&quot;Country&quot; id=&quot;contry-select&quot; :value.sync=&quot;country&quot; :options=&quot;countries&quot;&gt;&lt;/mdl-select&gt;</p></code><code class=javascript>new Vue({\n  data: {\n    country: 'Spain',\n    countries: [\n      {\n        name: 'Russia',\n        value: 'ru'\n      },\n      {\n        name: 'Spain',\n        value: 'es'\n      },\n      {\n      name: 'France',\n      value: 'fr'\n      },\n      {\n        name: 'China',\n        value: 'cn'\n      }\n    ]\n  }\n})\n</code></pre><p>MDL need an&nbsp;<code>id</code> to handle the menu toggling.</p></div></div>";


