module.exports = "<div class=section><title-link>Spinners</title-link><div class=section__content><p>Spinners allows you to inform the user that you're waiting for some responses</p><div class=\"flex center wrap\"><mdl-spinner class=space></mdl-spinner><mdl-spinner single-color=single-color class=space></mdl-spinner></div><pre><code class=html><p>&lt;mdl-spinner&gt;&lt;/mdl-spinner&gt;</p><p>&lt;mdl-spinner single-color&gt;&lt;/mdl-spinner&gt;</p></code></pre><p>You can deactivate the spinner by setting the<code> active</code> prop to<code> false</code></p><div class=\"flex start wrap\"><mdl-checkbox :checked.sync=active>Active</mdl-checkbox></div><div class=\"flex center wrap\"><mdl-spinner :active=active></mdl-spinner></div><pre><code class=html><p>&lt;mdl-spinner :active=&quot;active&quot;&gt;&lt;/mdl-spinner&gt;</p></code></pre><h5>Prop List</h5><table class=\"mdl-data-table mdl-js-data-table\"><thead><tr><th class=mdl-data-table__cell--non-numeric>Prop</th><th class=mdl-data-table__cell--non-numeric>Description</th><th class=mdl-data-table__cell--non-numeric>Remarks</th></tr></thead><tbody><tr><td class=mdl-data-table__cell--non-numeric><code> active</code></td><td class=mdl-data-table__cell--non-numeric>Control whether the spinner is active or not. Defaults to true</td><td class=mdl-data-table__cell--non-numeric>This only makes the component invisible</td></tr><tr><td class=mdl-data-table__cell--non-numeric><code> single-color</code></td><td class=mdl-data-table__cell--non-numeric>Applies a single color effect</td><td class=mdl-data-table__cell--non-numeric></td></tr></tbody></table><br/></div></div>";


