module.exports = "<div class=section><title-link>Dialogs</title-link><div class=section__content><p>The dialog component allows for verification of user actions, simple data input, and alerts to provide extra information to users. To display dialogs, use events.</p><div v-transfer-dom=v-transfer-dom>Hello<mdl-dialog title=\"Hi there\" display-on=infoMessage cancellable=cancellable><p>Hello. This is an information message. You can click outside or in the close button to close it.</p></mdl-dialog></div><div class=\"flex center wrap\"><mdl-button fab=fab primary=primary v-on:click=$broadcast(&quot;infoMessage&quot;)><i class=material-icons>power_settings_new</i></mdl-button></div><pre><code class=html><p>&lt;mdl-dialog title=&quot;Hi there&quot; display-on=&quot;infoMessage&quot; cancellable&gt;</p><p>  &lt;p&gt;Hello. This is an information message. You can click outside or in the close button to close it.&lt;/p&gt;</p><p>&lt;/mdl-dialog&gt;</p></code><code class=javascript>// Inside the parent vm\nthis.$broadcast('infoMessage')\n</code></pre><p>Sometimes the modal need to be appended to the dom. In order to do that you you'll need the <a href=https://github.com/rhyzx/vue-transfer-dom><code>vue-transfer-dom</code> directive</a>and include you dialog in an outter container:</p><pre><code class=html><p>&lt;div v-transfer-dom&gt;</p><p>  &lt;mdl-dialog title=&quot;Hi there&quot; display-on=&quot;infoMessage&quot; cancellable&gt;</p><p>    &lt;p&gt;Hello. This is an information message. You can click outside or in the close button to close it.&lt;/p&gt;</p><p>  &lt;/mdl-dialog&gt;</p><p>&lt;/div&gt;</p></code></pre><p>The example above use this technique to prevent the menu from overlapping.</p><p>By default there's only one action. You can add more actions by using the slot<code> actions</code>. Each action must either be a<code> button</code> or contain an attribute<code> data-action</code>.</p><pre><code class=html><p>&lt;mdl-dialog title=&quot;Hi there&quot; display-on=&quot;infoMessage&quot;&gt;</p><p>  &lt;p&gt;Hello&lt;/p&gt;</p><p>  &lt;template slot=&quot;actions&quot;&gt;</p><p>    &lt;mdl-button primary&gt;Increase&lt;/mdl-button&gt;</p><p>    &lt;mdl-button primary&gt;Decrease&lt;/mdl-button&gt;</p><p>    &lt;mdl-button&gt;Close&lt;/mdl-button&gt;</p><p>  &lt;/template&gt;</p><p>&lt;/mdl-dialog&gt;</p></code></pre><p>You can pass any number of callbacks as actions in the same order as the buttons when emitting the event.</p><pre><code class=javascript>// Inside the parent vm\nthis.$broadcast('infoMessage', increaseFunction, decreaseFunction, closeCallback)\n</code></pre><p>If you added the <code>cancellable</code> prop to the dialog, you can specify one extra callback. If there's no extra callback, the callback used for the last action is used. This callback is only called when the dialog is dismissed by clicking outside of it.</p><pre><code class=javascript>// Inside the parent vm\nthis.$broadcast('infoMessage', actionFunc, closeCallback, dismissCallback)\n</code></pre><p>By default all actions closes the dialog, if you want to prevent that you must explicitly return false in the callback.</p><p>You can pass <code>null</code> instead of a function for any given action.</p><h5>Prop List</h5><table class=\"mdl-data-table mdl-js-data-table\"><thead><tr><th class=mdl-data-table__cell--non-numeric>Prop</th><th class=mdl-data-table__cell--non-numeric>Description</th><th class=mdl-data-table__cell--non-numeric>Remarks</th></tr></thead><tbody><tr><td class=mdl-data-table__cell--non-numeric><code> title</code></td><td class=mdl-data-table__cell--non-numeric>Title of the dialog</td><td class=mdl-data-table__cell--non-numeric>Optional</td></tr><tr><td class=mdl-data-table__cell--non-numeric><code> display-on</code></td><td class=mdl-data-table__cell--non-numeric>Event to listen for displaying</td><td class=mdl-data-table__cell--non-numeric>Required</td></tr><tr><td class=mdl-data-table__cell--non-numeric><code> full-width</code></td><td class=mdl-data-table__cell--non-numeric>Make buttons full width by applying the <code> mdl-dialog__actions--full-width</code> modifier</td><td class=mdl-data-table__cell--non-numeric>This prop doesn't need a value</td></tr><tr><td class=mdl-data-table__cell--non-numeric><code> cancellable</code></td><td class=mdl-data-table__cell--non-numeric>Make the dialog dismissible by clicking outside of it</td><td class=mdl-data-table__cell--non-numeric>This prop doesn't need a value</td></tr></tbody></table><br/></div></div>";


