module.exports = "<div class=section><title-link>Snackbars</title-link><div class=section__content><p>Snackbars are containers used to notify a user of operation's status. It displays at the bottom of the screen. A snackbar may contain an action button to execute a command for the user. Actions should undo the committed action or retry it if it failed for example. Actions should not be to close the snackbar. By not providing an action, the snackbar becomes a toast component.</p><div class=\"flex center wrap\"><mdl-snackbar display-on=mailSent></mdl-snackbar></div><div class=\"flex center wrap\"><mdl-button raised=raised colored=colored @click=\"$broadcast(&quot;mailSent&quot;, { message: &quot;Message Sent&quot; })\">Show</mdl-button></div><pre><code class=html><p>&lt;mdl-snackbar display-on=&quot;mailSent&quot;&gt;&lt;/mdl-snackbar&gt;</p><p>&lt;mdl-button raised colored @click=&quot;$broadcast('mailSent', { message: 'Message Sent' })&quot;&gt;Show&lt;/mdl-button&gt;</p></code></pre><p>The object passed when broadcasting the event <code>mailSent</code>, is the data passsed to the <a target=_blank href=http://www.getmdl.io/components/index.html#snackbar-section><code>showSnackbar</code> function</a>. For instance you can pass a callback for an action:</p><div class=\"flex center wrap\"><mdl-snackbar display-on=colorChanged></mdl-snackbar></div><div class=\"flex center wrap\"><mdl-button :style=buttonStyle raised=raised @click=changeColor>Change color</mdl-button></div><pre><code class=html><p>&lt;mdl-snackbar display-on=&quot;colorChanged&quot;&gt;&lt;/mdl-snackbar&gt;</p><p>&lt;mdl-button raised :style=&quot;buttonStyle&quot; @click=&quot;changeColor&quot;&gt;Change color&lt;/mdl-button&gt;</p></code><code class=javascript>new Vue({\n  data: {\n    color: null\n  },\n  computed: {\n    buttonStyle () {\n      if (this.color) {\n        return {\n          'background-color': this.color\n        }\n      } else {\n        return {}\n      }\n    }\n  },\n  methods: {\n    changeColor () {\n      this.color = `#${Math.floor(Math.random() * 0xFFFFFF).toString(16)}`\n      this.$broadcast('colorChanged', {\n        message: 'Color changed',\n        actionHandler: (event) => {\n          this.color = null\n        },\n        actionText: 'Undo',\n        timeout: 2000\n      })\n    }\n  }\n}\n</code></pre><p>If you use multiple snackbars at the same time, they will overlap. Click on both button to see an example. If you don't want this to happen use one single snackbar for all your notifications</p></div></div>";


