module.exports = "<div class=section><title-link>Badges</title-link><div class=section__content><p>Badges let you add onscreen notifications. They are made to be used on <code>i</code>, <code>span</code> and <code>a</code> elements</p><div class=\"flex start wrap\"><span v-mdl-badge=value>Inbox</span><span v-mdl-badge.no-background=value>Inbox</span><i v-mdl-badge.overlap=text class=material-icons>account_box</i></div><div class=\"flex start wrap\"><mdl-textfield :value.sync=value label=\"Badge Number\" floating-label=floating-label></mdl-textfield><mdl-textfield :value.sync=text label=\"Badge Text\" floating-label=floating-label></mdl-textfield></div><p>Pass the text you want to the directive:</p><pre><code class=html><p>&lt;span v-mdl-badge=&quot;value&quot;&gt;Inbox&lt;/span&gt;</p><p>&lt;span v-mdl-badge.no-background=&quot;value&quot;&gt;Inbox&lt;/span&gt;</p><p>&lt;i class=&quot;material-icons&quot; v-mdl-badge.overlap=&quot;text&quot;&gt;account_box&lt;/i&gt;</p></code></pre><p>You'll need to add this css to make the <code>overlap</code> modifier work in mdl lower than 1.0.7</p><pre><code class=css>.mdl-badge.mdl-badge--overlap {\n  margin-right: 10px;\n}\n.mdl-badge.mdl-badge--overlap:after {\n  right: -10px;\n}\n</code></pre><p>You can automatically hide the badge depending on the value using the <code>hide-badge</code> param. If you want to hide the badge when the value is a number and it's below or equal to 0, you can use the <code>number</code> modifier instead. You can also combine both of them</p><div><span v-mdl-badge=num :hide-badge=zero>Inbox</span><span v-mdl-badge.number=num :hide-badge=hide>Inbox</span></div><div class=\"flex start wrap\"><mdl-slider min=0 max=15 v-bind:value.sync=num></mdl-slider><mdl-checkbox :checked.sync=hide>Hide</mdl-checkbox></div><pre><code class=html><p>&lt;span v-mdl-badge=&quot;num&quot;&gt;Inbox&lt;/span&gt;</p><p>&lt;span v-mdl-badge.number=&quot;num&quot; :hide-badge=&quot;hide&quot;&gt;Inbox&lt;/span&gt;</p></code><code class=javascript>new Vue({\n  data: {\n    num: 3,\n    hide: true\n  },\n  computed: {\n    zero: function() {\n      return parseInt(this.num, 10) &lt;= 0\n    }\n  }\n})\n</code></pre><table class=\"mdl-data-table mdl-js-data-table\"><thead><tr><th class=mdl-data-table__cell--non-numeric>Modifiers</th><th class=mdl-data-table__cell--non-numeric>Description</th><th class=mdl-data-table__cell--non-numeric>Remarks</th></tr></thead><tbody><tr><td class=mdl-data-table__cell--non-numeric><code>no-background</code></td><td class=mdl-data-table__cell--non-numeric>Applies open-circle effect to badge by adding the<code>mdl-badge--no-background</code> class</td><td class=mdl-data-table__cell--non-numeric>It actually switches primary and accent colorls. So you will have to add some css to make the desired open-circle effect</td></tr><tr><td class=mdl-data-table__cell--non-numeric><code>overlap</code></td><td class=mdl-data-table__cell--non-numeric>Make the badge overlap with its container by adding the<code>mdl-badge--overlap</code> class</td><td class=mdl-data-table__cell--non-numeric>mdl >= 1.0.6</td></tr><tr><td class=mdl-data-table__cell--non-numeric><code>number</code></td><td class=mdl-data-table__cell--non-numeric>Automatically hide the badge when the given value isn't a number or is below or equal to 0</td><td class=mdl-data-table__cell--non-numeric>When using the <code>hide-badge</code> param, this modifier has lower priority</td></tr></tbody></table></div></div>";


